.work{
    width: 500px;
    border: 1px solid grey;
    background-color: #010001;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    border-radius: 6px 6px 6px 6px;
    transition: all 0.4s ease;
}

.work:hover{
    transform: translateY(-5px);
    transition: all 0.4s ease;
    -moz-box-shadow: 5px 5px 25px rgba(255,0,0,0.2);
    -webkit-box-shadow: 0 0 25px rgba(255,0,0,0.2);
    box-shadow: 0 0 25px rgba(255,0,0,0.2);
}

.showBtn{
    display: block;
    position: relative;
    top: 40%;
    left: 35%;
    opacity: 0;
    transition: all 1s ease;
    color: #fff;
    font-size: 16px;
    padding: 16px 40px;
    border-radius: 500px;
    font-weight: 500;
    background-size: 152% 100%;
    background: #fd4766;
    border: 2px solid #fd4766;
    cursor: pointer;
    font-family: 'Poppins', sans-serif;
    z-index: 1;
}

.image:hover .showBtn{
    opacity: 1;
    transition: all 1s ease;
}

.showBtn:hover{
    background: black;
    color: #fd4766;
}

.image{
    position: relative;
    height: 300px;
    width: 500px;
    background-color: gray;
    background-size: cover;
    background-position: center;
    border-radius: 6px 6px 0 0;
    -moz-transition: background-color 0.8s 0.1s ease;
    -o-transition: background-color 0.8s 0.1s ease;
    -webkit-transition: background-color 0.8s 0.1s ease;
    cursor: pointer;
}

.image::before{
    position: absolute;
    content: '';
    background-color: black;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    opacity: 0.6;
}

.info{
    padding: 20px;
    text-align: start;
}

.description{
    margin: 0;
    color: #757575;
    font-family: 'Poppins', sans-serif;
}

.title{
    margin: 0;
    padding-bottom: 10px;
    font-weight: 700;
    font-size: 18px;
    color: #fff;
    font-family: 'Poppins', sans-serif;
}


@media (max-width: 754px) {
    .image{
        height: 250px;
        width: 250px;
    }
}