.skill{
    padding: 10px;
    width: 250px;
    border: 1px solid gray;
    background-color: #010001;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    border-radius: 6px;
    transition: all 0.4s ease;
}

.skill:hover{
    transform: translateY(-5px);
    transition: all 0.8s ease;
    -moz-box-shadow: 5px 5px 25px rgba(255,0,0,0.2);
    -webkit-box-shadow: 0 0 25px rgba(255,0,0,0.2);
    box-shadow: 0 0 25px rgba(255,0,0,0.2);
}

.icon{
    display: flex;
    left: 0;
    height: 50px;
    justify-content: flex-start;
    width: 200px;
}

i{
    font-size: 6.5vh;
    color: #c542ff;
}

.info{
    padding: 20px;
    text-align: left;
}

h3{
    padding-bottom: 10px;
    margin: 0;
    color: #fff;
    font-weight: 700;
    font-size: 18px;
}

span{
    margin: 0;
    color: #757575;
}
